<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 面包导航区结束 -->

    <!-- 卡片视图区域 -->
    <el-card class="box-card">
      <!-- 添加分类按钮 -->
      <el-row>
        <el-col>
          <el-button
            type="success"
            icon="el-icon-circle-plus-outline"
            @click="showAddGoodsCateDialog"
            >添加分类</el-button
          >
        </el-col>
      </el-row>

      <!-- 角色数据展示区域 -->
      <tree-table
        class="tree-table"
        :data="goodsCateList"
        :columns="goodsCateColumn"
        border
        :show-row-hover="false"
        :selection-type="false"
        show-index
        index-text="#"
        :expand-type="false"
      >
        <!-- 是否有效的模板 -->
        <template slot="isOK" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen"
          ></i>
          <i class="el-icon-error" v-else style="color: red"></i>
        </template>

        <!-- 排序的模板 -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0">一级</el-tag>
          <el-tag
            size="mini"
            type="success"
            v-else-if="scope.row.cat_level === 1"
            >二级</el-tag
          >
          <el-tag size="mini" type="warning" v-else>三级</el-tag>
        </template>
        <!-- 操作的模板 -->
        <template slot="opt" slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditGoodsNameDialog(scope.row)"
            >编辑</el-button
          >
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteGoodsCate(scope.row.cat_id)"
            >删除</el-button
          >
        </template>
      </tree-table>

      <!-- 数据分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 添加商品分类的弹出框 -->
    <el-dialog
      title="添加分类"
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="resetAddGoosCate"
    >
      <el-form
        ref="addCateForm"
        :model="addCateForm"
        label-width="100px"
        :rules="addCateFormRules"
      >
        <el-form-item label="分类名称：" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类：">
          <!-- 级联输入框 -->
          <el-cascader
            v-model="selectedCate"
            :options="goodsParentCateList"
            :props="cateProps"
            @change="handleChange"
            clearable
            style="width: 100%"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetAddGoosCate">取 消</el-button>
        <el-button type="primary" @click="addGoodsCate"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <!-- 编辑商品分类名的弹出框 -->
    <el-dialog
      title="提示"
      :visible.sync="editGoodsNameDialogVisible"
      width="50%"
      @close="reseteditGoosName"
    >
      <el-form
        ref="editCateForm"
        :model="editCateForm"
        label-width="100px"
        :rules="editCateFormRules"
      >
        <el-form-item label="分类名称：" prop="cat_name">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="reseteditGoosName">取 消</el-button>
        <el-button type="primary" @click="editGoodsCate"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 默认的商品分类查询参数
      queryInfo: {
        type: null,
        pagenum: 1,
        pagesize: 3
      },
      // 总计商品分类的条数
      total: 0,
      // 商品分类列表的数据按分页查询
      goodsCateList: [],
      // 配置每列数据的渲染格式
      goodsCateColumn: [
        {
          label: '分类',
          prop: 'cat_name'
        },
        {
          label: '是否有效',
          prop: '',
          type: 'template',
          template: 'isOK'
        },
        {
          label: '排序',
          prop: '',
          type: 'template',
          template: 'order'
        },
        {
          label: '操作',
          prop: '',
          type: 'template',
          template: 'opt'
        }
      ],
      // 添加商品分类的弹出框控制
      addCateDialogVisible: false,
      // 所有商品父级的数据列表
      goodsParentCateList: [],
      // 添加商品分类的数据表单
      addCateForm: {
        cat_pid: 0,
        cat_name: '',
        cat_level: 0
      },
      // 输入的分类名的验证规则
      addCateFormRules: {
        cat_name: [
          { required: true, message: '请填写分类名称', trigger: 'blur' }
        ]
      },
      // 已经选中的分类的id为一个数组
      selectedCate: [],
      // 级联选择框的配置
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: 'true'
      },
      // 控制编辑商品分类名的弹出框
      editGoodsNameDialogVisible: false,
      // 编辑分类名的数据表单
      editCateForm: {
        cat_name: ''
      },
      // 编辑商品分类名的表单验证规则
      editCateFormRules: {
        cat_name: [
          { required: true, message: '请输入需要修改的分类名称', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    // 初始商品分类的数据
    this.getGoodsCateList()
  },
  methods: {
    // 获取所有的商品的分类数据
    async getGoodsCateList() {
      // 获取所有的商品分类参数
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('商品分类列表获取错误')
      }
      // 初始化商品分类的数据
      this.goodsCateList = res.data.result
      // 初始化total的值
      this.total = res.data.total
    },
    // 获取所有商品的父级分类
    async getParentCateList() {
      // 获取父级分类
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) { return this.$message.error('获取父级列表失败') }
      // 将数据用于展示到级联选择框中
      this.goodsParentCateList = res.data
    },
    // 处理每页的数据条数的变化
    handleSizeChange(val) {
      // 将最新值的数量赋给查询条件
      this.queryInfo.pagesize = val
      // 然后重新查询，获取数据
      this.getGoodsCateList()
    },
    // 处理当前的页码的变化
    handleCurrentChange(val) {
      // 将最新值的页码赋给查询条件
      this.queryInfo.pagenum = val
      // 然后重新查询，获取数据
      this.getGoodsCateList()
    },
    // 显示添加分类的弹出框
    showAddGoodsCateDialog() {
      // 先初始数据
      this.getParentCateList()
      // 然后显示添加分类弹出框
      this.addCateDialogVisible = true
    },
    // 级联选择框变化的数据处理
    handleChange() {
      // 判断是否选择此框 未选择则为初始值
      if (this.selectedCate.length === 0) {
        this.addCateForm.cat_pid = 0
        // 分类等级获取
        this.addCateForm.cat_level = 0
        return null
      }
      // 获取当前添加分类的父级id
      this.addCateForm.cat_pid = this.selectedCate[this.selectedCate.length - 1]
      // 分类等级获取
      this.addCateForm.cat_level = this.selectedCate.length
    },
    // 重置表单的内容
    resetAddGoosCate() {
      // 将输入的分类名清空
      this.$refs.addCateForm.resetFields()
      // 同时清空级联选择框
      this.selectedCate = []
      // 清空提交的查询参数
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },
    // 添加分类
    addGoodsCate() {
      // 先验证表单是否通过
      this.$refs.addCateForm.validate(async vaild => {
        if (!vaild) return null
        // 添加商品分类
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) return this.$message.error('添加商品分类失败')
        this.$message.success('添加商品分类成功')
        // 关闭弹出框
        this.addCateDialogVisible = false
        // 刷新页面的数据
        this.getGoodsCateList()
      })
    },
    // 根据id查询商品分类的信息
    async getGoodsCateInfoById(id) {
      // 获取数据
      const { data: res } = await this.$http.get(`categories/${id}`)
      if (res.meta.status !== 200) return this.$message.error('查询本次商品分类数据失败')
      this.editCateForm = res.data
    },
    // 显示编辑商品分类的弹窗
    showEditGoodsNameDialog(goods) {
      // 获取点击商品分类信息
      this.getGoodsCateInfoById(goods.cat_id)
      this.editGoodsNameDialogVisible = true
    },
    // 重置商品分类名的表单数据
    reseteditGoosName() {
      this.editGoodsNameDialogVisible = false
      // 重置表单的内容和验证规则
      this.$refs.editCateForm.resetFields()
    },
    // 编辑商品的名字
    editGoodsCate() {
      // 先验证表单是否通过验证
      this.$refs.editCateForm.validate(async vaild => {
        if (!vaild) return null
        const { data: res } = await this.$http.put(`categories/${this.editCateForm.cat_id}`, {
          cat_name: this.editCateForm.cat_name
        })
        if (res.meta.status !== 200) { return this.$message.error('商品分类名称修改失败') }
        this.$message.success('商品分类名称修改成功')
        // 关闭弹窗
        this.editGoodsNameDialogVisible = false
        // 刷新页面数据
        this.getGoodsCateList()
      })
    },
    // 删除商品分类
    async deleteGoodsCate(id) {
      // 弹窗多次确认
      const result = await this.$confirm(
        '此操作将永久删除该商品分类, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      if (result === 'cancel') {
        return null
      }
      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      // 刷新数据
      this.getGoodsCateList()
    }
  }
}
</script>

<style lang="less" scoped>
// 表格的上下距离
.tree-table {
  margin: 15px 0;
}
</style>
