<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 面包导航区结束 -->

    <!-- 卡片视图区域 -->
    <el-card class="box-card">
      <!-- 带搜索的输入框 根据商品名搜索商品数据 -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入商品名"
            v-model="queryInfo.query"
            clearable
            @clear="getGoodsList"
            @blur="searchGoodsList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchGoodsList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button
            type="success"
            icon="el-icon-circle-plus-outline"
            @click="goTOAddGoods"
            >添加商品</el-button
          >
        </el-col>
      </el-row>
      <!-- 商品数据展示区域 -->
      <el-table :data="goodsList" stripe style="width: 100%" border>
        <!-- 索引效果 -->
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="goods_name" label="商品名称">
        </el-table-column>
        <el-table-column prop="goods_price" label="价格" width="95px">
        </el-table-column>
        <el-table-column prop="goods_number" label="数量" width="95px">
        </el-table-column>
        <el-table-column prop="goods_weight" label="重量" width="95px">
        </el-table-column>
        <el-table-column label="添加时间" width="180px">
            <template slot-scope="scope">
                {{scope.row.add_time | cqDateFormat}}
            </template>
        </el-table-column>
        <el-table-column label="操作" width="180px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
            >编辑</el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteGoods(scope.row)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页功能区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 商品查询参数
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 商品总条数
      total: 0,
      // 商品的数据
      goodsList: [],
      // 是否点击了搜索按钮
      isSearch: false
    }
  },
  created() {
    //   初始化表格的数据
    this.getGoodsList()
  },
  methods: {
    async getGoodsList() {
      // 并且重置页码数 若输入了参数代表搜索框有值
      // 把页码重置为1
      if (this.isSearch) {
        this.queryInfo.pagenum = 1
        // 每次查询结束把搜索按钮重置为未点击状态
        this.isSearch = false
      }
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error('获取商品列表数据失败')
      // 把获取商品列表数据储存起来
      this.goodsList = res.data.goods
      // 获取的总文档数得到
      this.total = res.data.total
    },
    // 搜索操作
    searchGoodsList() {
      // 搜索了就把搜索按钮赋值为真
      this.isSearch = true
      this.getGoodsList()
    },
    // 页面数量改变
    handleSizeChange(pageSize) {
      // 赋值给查询当前需要显示的商品数量
      this.queryInfo.pagesize = pageSize
      // 然后重新获取数据
      this.getGoodsList()
    },
    // 页面页码改变
    handleCurrentChange(pageNum) {
      // 赋值当前的页码数
      this.queryInfo.pagenum = pageNum
      // 然后重新获取数据
      this.getGoodsList()
    },
    // 删除数据
    async deleteGoods(row) {
      // 弹窗多次确认
      const result = await this.$confirm(
        '此操作将永久删除该商品, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      if (result === 'cancel') {
        return null
      }
      const { data: res } = await this.$http.delete('goods/' + row.goods_id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success('删除所选商品成功')
      // 刷新数据
      this.getGoodsList()
    },
    // 点击前往添加商品页面
    goTOAddGoods() {
      this.$router.push('/goods/add')
    }
  }
}
</script>

<style lang="less" scoped>
</style>
