<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 面包导航区结束 -->

    <!-- 卡片视图区域 -->
    <el-card class="box-card">
      <!-- 提示信息 -->
      <el-alert
        title="添加商品"
        type="warning"
        center
        show-icon
        :closable="false"
      >
      </el-alert>

      <!-- 步骤条 -->
      <el-steps
        :active="nowStep - 0"
        finish-status="success"
        simple
        style="margin-top: 20px"
        process-status="finish"
      >
        <el-step title="基本信息" icon="el-icon-edit"></el-step>
        <el-step title="商品参数" icon="el-icon-menu"></el-step>
        <el-step title="商品属性" icon="el-icon-menu"></el-step>
        <el-step title="商品图片" icon="el-icon-picture"></el-step>
        <el-step title="商品内容" icon="el-icon-document"></el-step>
      </el-steps>

      <!-- 将tab栏里面填写的所有数据保存到表单中 -->
      <el-form
        ref="addGoodsForm"
        :model="addGoodsForm"
        label-width="auto"
        :rules="addGoodsFormRules"
        label-position="top"
      >
        <!-- tab栏切换效果 -->
        <el-tabs
          tab-position="left"
          v-model="nowStep"
          :before-leave="handleTabChange"
          @tab-click="getAttrListById"
        >
          <!-- 基本信息填写 -->
          <el-tab-pane name="0">
            <span slot="label"><i class="el-icon-edit"></i> 基本信息</span>
            <el-form-item label="商品名称：" prop="goods_name">
              <el-input v-model="addGoodsForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格：" prop="goods_price">
              <el-input v-model.number="addGoodsForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="商品数量：" prop="goods_number">
              <el-input v-model.number="addGoodsForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量：" prop="goods_weight">
              <el-input v-model.number="addGoodsForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="商品分类（Tips：只能选择第三级分类）：">
              <!-- 级联输入框 -->
              <el-cascader
                v-model="addGoodsForm.goods_cat"
                :options="goodsCateList"
                :props="cateProps"
                @change="handleChange"
                clearable
                placeholder="请选择商品分类"
                style="width: 25%"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!-- 商品参数 -->
          <el-tab-pane name="1">
            <span slot="label"><i class="el-icon-menu"></i> 商品参数</span>
            <el-form-item
              v-for="(attr, index) in manyAttrList"
              :label="attr.attr_name"
              :key="index"
            >
              <el-checkbox-group
                v-model="attr.manySelectedAttr"
                @change="handleCahange"
              >
                <el-checkbox
                  v-for="(attr1, index1) in attr.attr_vals"
                  :label="attr1"
                  :key="index1"
                  checked
                  >{{ attr1 }}</el-checkbox
                >
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!-- 商品属性 -->
          <el-tab-pane name="2">
            <span slot="label"><i class="el-icon-menu"></i> 商品属性</span>
            <el-form-item
              v-for="(attr, index) in onlyAttrList"
              :label="attr.attr_name"
              :key="index"
            >
              <el-checkbox-group
                v-model="attr.onlySelectedAttr"
                @change="handleCahange"
              >
                <el-checkbox
                  v-for="(attr1, index1) in attr.attr_vals"
                  :label="attr1"
                  :key="index1"
                  checked
                  >{{ attr1 }}</el-checkbox
                >
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!-- 上传图片 -->
          <el-tab-pane name="3">
            <span slot="label"><i class="el-icon-picture"></i> 商品图片</span>
            <!-- 图片上传框 -->
            <el-upload
              class="upload-demo"
              :action="actionUrl"
              :headers="headers"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture"
              drag
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                将文件拖到此处，或<em>点击上传</em>
              </div>
              <div slot="tip" class="el-upload__tip">
                只能上传jpg/png文件，且不超过500kb
              </div>
            </el-upload>
            <!-- 图片预览框 -->
            <el-dialog
              title="图片预览"
              :visible.sync="previewImgDialog"
              width="60%"
              @close="previewImgDialog = false"
            >
              <img
                :src="this.previewImgUrl"
                alt="图片预览"
                style="width: 100%"
              />
            </el-dialog>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <span slot="label"><i class="el-icon-document"></i> 商品内容</span>
            <quill-editor
              v-model="addGoodsForm.goods_introduce"
            />
            <el-button type="success" class="add-btn" @click="addGoods">添加</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
  </div>
</template>
<script>

// 导入lodash
import _ from 'lodash'

export default {
  data() {
    return {
      // tab栏和进度栏选中的索引值
      nowStep: '0',
      // 添加商品数据的表单绑定对象
      addGoodsForm: {
        goods_name: '',
        goods_cat: [],
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_introduce: '',
        pics: [],
        attrs: []
      },
      // 添加商品表单的数据验证规则
      addGoodsFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入价格', trigger: 'blur' },
          { type: 'number', message: '请输入数字', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入数量', trigger: 'blur' },
          { type: 'number', message: '请输入数字', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入重量', trigger: 'blur' },
          { type: 'number', message: '请输入数字', trigger: 'blur' }
        ]
      },
      // 商品所有的分类数据
      goodsCateList: [],
      // 级联选择框的配置
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 选择分类的所有动态参数
      manyAttrList: [],
      // 选择分类的所有静态属性
      onlyAttrList: [],
      // 图片上传的服务器地址
      actionUrl: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 上传头部附带的token
      headers: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 当前预览图片的路径
      previewImgUrl: '',
      // 图片预览框弹出控制
      previewImgDialog: false
    }
  },
  created() {
    // 初始化商品分类的数据列表
    this.getGoodsCateList()
  },
  methods: {
    // 获取列表中的所有的商品分类列表
    async getGoodsCateList() {
      // 获取所有商品分类的数据
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类失败')
      }
      // 将数据储存到数组
      this.goodsCateList = res.data
    },
    // 级联框选择值的操作
    handleChange() {
      // 只允许选中第三级的商品分类
      // 若选中的不是第三级的数据，则重置选择框为未选中的状态
      if (this.addGoodsForm.goods_cat.length !== 3) {
        this.addGoodsForm.goods_cat = []
        return null
      }
      console.log(this.addGoodsForm.goods_cat)
    },
    // 处理tab栏切换时的阻止
    handleTabChange(activeName, oldActiveName) {
      // 在tab栏切换之前触发，两个形参为切换前，后的tab栏name
      if (oldActiveName === '0') {
        // 在第一个标签页的时候
        // 必须把当前商品添加的基本信息填完才能填写其他的
        if (this.addGoodsForm.goods_cat.length !== 3) {
          this.$message.error('请选择商品的分类')
          return false
        } else if (this.addGoodsForm.goods_name.trim() === '') {
          this.$message.error('请输入商品名称')
          return false
        } else if (this.addGoodsForm.goods_price < 0) {
          this.$message.error('请输入正确商品价格')
          return false
        } else if (this.addGoodsForm.goods_weight < 0) {
          this.$message.error('请输入正确商品重量')
          return false
        } else if (this.addGoodsForm.goods_number < 0) {
          this.$message.error('请输入正确商品数量')
          return false
        }
      }
    },
    // 获取商品分类对应的属性名
    async getAttrListById() {
      // 获取动态参数 点击name为1时
      if (this.nowStep === '1' && this.manyAttrList.length === 0) {
        // 选择第三级商品分类就获取数据
        const { data: res } = await this.$http.get(
          `categories/${this.getCateId}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取商品参数失败')
        }
        // 将数据里面的attr_vals变成数组格式的数据
        res.data.forEach((item) => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          // 绑定一个选中的值
          item.manySelectedAttr = []
        })
        this.manyAttrList = res.data
      } else if (this.nowStep === '2' && this.onlyAttrList.length === 0) {
        // 选择第三级商品分类就获取数据
        const { data: res } = await this.$http.get(
          `categories/${this.getCateId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取商品属性失败')
        }
        // 将数据里面的attr_vals变成数组格式的数据
        res.data.forEach((item) => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          // 绑定一个选中的值
          item.onlySelectedAttr = []
        })
        this.onlyAttrList = res.data
      }
    },
    // 处理选中框的变化
    handleCahange() {
      // console.log(this.manySelectedAttr)
      // console.log(this.onlyAttrList)
    },
    // 处理图片预览
    handlePreview(file) {
      // 获得当前点击图片的服务器url
      this.previewImgUrl = file.response.data.url
      // 打开弹出框
      this.previewImgDialog = true
    },
    // 处理图片移除
    handleRemove(file) {
      // 将移除的图片找到，然后移除
      // 图片路径
      // file.response.data.tmp_path
      // 获取移除的图片的路径
      const removeImg = file.response.data.tmp_path
      // 获取需要移除的下标
      const removeIndex = this.addGoodsForm.pics.findIndex(
        (item) => item.pic === removeImg
      )
      // 去除移除图片的路径
      this.addGoodsForm.pics.splice(removeIndex, 1)
    },
    // 图片上传成功的处理
    handleSuccess(response) {
      // 将相对路径存入pics数组中
      this.addGoodsForm.pics.push({ pic: response.data.tmp_path })
    },
    // 添加商品
    addGoods() {
      // 先验证表单
      this.$refs.addGoodsForm.validate(async vaild => {
        if (!vaild) {
          return null
        }
        // 将表单的数据获取一份
        const newAddGoodsParams = _.cloneDeep(this.addGoodsForm)
        // 处理goods_cat
        newAddGoodsParams.goods_cat = newAddGoodsParams.goods_cat.join(',')
        // 处理atrrs数据
        // 处理动态参数
        this.manyAttrList.forEach(item => {
          newAddGoodsParams.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.manySelectedAttr.join(' ')
          })
        })
        // 处理静态属性
        this.onlyAttrList.forEach(item => {
          newAddGoodsParams.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.onlySelectedAttr.join(' ')
          })
        })
        // 然后进行添加商品操作
        const { data: res } = await this.$http.post('goods', newAddGoodsParams)
        if (res.meta.status !== 201) return this.$message.error('添加商品失败')
        // 添加商品成功并跳转到主页
        this.$message.success('添加商品成功')
        // 跳转到商品页面
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    // 获取当前选中的商品分类的id
    getCateId() {
      // 如果没有选择第三级商品分类
      // 返回空
      if (this.addGoodsForm.goods_cat.length !== 3) return null
      // 选择了就会返回绑定数组中的最后一项
      //   实现第三级商品分类的id返回
      return this.addGoodsForm.goods_cat[this.addGoodsForm.goods_cat.length - 1]
    }
  }
}
</script>

<style lang="less" scoped>
// 图片列表的样式处理
/deep/.el-upload-list__item {
  float: left;
  width: 30%;
  margin-right: 15px;
}

// 富集框长度
/deep/.ql-container {
  min-height: 350px;
}

// 添加按钮格式
.add-btn {
  margin-top: 15px;
}
</style>
