<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>分类参数</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 面包导航区结束 -->

    <!-- 卡片视图区域 -->
    <el-card class="box-card">
      <!-- 警告框 -->
      <!-- 只允许选择第三级的分类名称才能添加动态 -->
      <el-alert
        title="注意：只允许为第三级分类设置相关参数"
        type="warning"
        show-icon
      >
      </el-alert>

      <!-- 选择商品的区域 -->
      <el-row class="select-goods">
        <el-col>
          <span>选择商品分类：</span>
          <!-- 商品分类的选择级联框 -->
          <!-- 级联输入框 -->
          <el-cascader
            v-model="selectedCate"
            :options="goodsCateList"
            :props="cateProps"
            @change="handleChange"
            clearable
            placeholder="请选择商品分类"
            style="width: 25%"
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- tab栏切换实现动态参数和静态属性的设置 -->
      <el-row class="select-goods">
        <el-col>
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="动态参数" name="many">
              <!-- 添加参数按钮 -->
              <el-button
                type="success"
                icon="el-icon-circle-plus-outline"
                size="mini"
                :disabled="isBtnDisabled"
                @click="showAddParamsDialog"
                >添加{{ getActiveTabName }}</el-button
              >
              <!-- 对应的table表格数据渲染 -->
              <el-table :data="manyAttrList" stripe style="width: 100%" border>
                <!-- 展开列 -->
                <el-table-column type="expand">
                  <template slot-scope="scope">
                    <el-tag
                    :key="tag"
                    v-for="(tag, i) in scope.row.attr_vals"
                    closable
                    @close="handleClose(i, scope.row)"
                  >
                    {{ tag }}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  >
                  </el-input>
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scope.row)"
                    >+ New Tag</el-button
                  >
                  </template>
                </el-table-column>
                <!-- 索引效果 -->
                <el-table-column type="index" label="#"> </el-table-column>
                <el-table-column prop="attr_name" label="名称">
                </el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button
                      type="primary"
                      icon="el-icon-edit"
                      size="mini"
                      @click="showEditParamsDialog(scope.row)"
                      >编辑</el-button
                    >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      @click="deleteParams(scope.row)"
                      >删除</el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="静态属性" name="only">
              <!-- 添加参数按钮 -->
              <el-button
                type="success"
                icon="el-icon-circle-plus-outline"
                size="mini"
                :disabled="isBtnDisabled"
                @click="showAddParamsDialog"
                >添加{{ getActiveTabName }}</el-button
              >
              <!-- 对应的table表格数据渲染 -->
              <el-table :data="onlyAttrList" stripe style="width: 100%" border>
                <!-- 展开列 -->
                <el-table-column type="expand"> </el-table-column>
                <!-- 索引效果 -->
                <el-table-column type="index" label="#"> </el-table-column>
                <el-table-column prop="attr_name" label="名称">
                </el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button
                      type="primary"
                      icon="el-icon-edit"
                      size="mini"
                      @click="showEditParamsDialog(scope.row)"
                      >编辑</el-button
                    >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      @click="deleteParams(scope.row)"
                      >删除</el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </el-col>
      </el-row>
    </el-card>

    <!-- 添加属性的弹出框 -->
    <el-dialog
      :title="'添加' + getActiveTabName"
      :visible.sync="addParamsDialog"
      width="30%"
      @close="resetAddParams"
    >
      <el-form
        ref="addParamsForm"
        :model="addParamsForm"
        label-width="100px"
        :rules="addParamsFormRules"
      >
        <el-form-item :label="getActiveTabName + '：'" prop="attr_name">
          <el-input v-model="addParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetAddParams">取 消</el-button>
        <el-button type="primary" @click="addParams">添 加</el-button>
      </span>
    </el-dialog>
    <!-- 添加属性的弹出框 -->
    <el-dialog
      :title="'编辑' + getActiveTabName"
      :visible.sync="editParamsDialog"
      width="30%"
      @close="resetEditParams"
    >
      <el-form
        ref="editParamsForm"
        :model="editParamsForm"
        label-width="100px"
        :rules="editParamsFormRules"
      >
        <el-form-item :label="getActiveTabName + '：'" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetEditParams">取 消</el-button>
        <el-button type="primary" @click="editParams">修 改</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 商品所有的分类数据
      goodsCateList: [],
      // 级联选择框的配置
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 级联框的默认选中值
      selectedCate: [],
      // tab栏的选中值
      activeName: 'many',
      // 商品分类的动态参数
      manyAttrList: [],
      // 商品分类的静态属性
      onlyAttrList: [],
      // 添加参数的弹出框控制
      addParamsDialog: false,
      // 添加属性的参数值
      addParamsForm: {
        attr_name: '',
        attr_sel: 'many'
      },
      // 添加属性表单验证规则
      addParamsFormRules: {
        attr_name: [
          {
            required: true,
            message: '请填写需要添加的参数或属性',
            trigger: 'blur'
          }
        ]
      },
      // 编辑参数的弹出框控制
      editParamsDialog: false,
      // 编辑属性的参数值
      editParamsForm: {
        attr_name: ''
      },
      // 编辑属性表单验证规则
      editParamsFormRules: {
        attr_name: [
          {
            required: true,
            message: '请填写需要修改的参数或属性',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    // 初始商品分类选择的数据
    this.getGoodsCateList()
  },
  methods: {
    // 获取列表中的所有的商品分类列表
    async getGoodsCateList() {
      // 获取所有商品分类的数据
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类失败')
      }
      // 将数据储存到数组
      this.goodsCateList = res.data
    },
    // 获取商品分类对应的属性名
    async getAttrList() {
      // 只允许选中第三级的商品分类
      // 若选中的不是第三级的数据，则重置选择框为未选中的状态
      if (this.selectedCate.length !== 3) {
        this.selectedCate = []
        // 并把已经获取的商品属性的数据清空
        this.onlyAttrList = []
        this.manyAttrList = []
        return null
      }
      // 选择第三级商品分类就获取数据
      const { data: res } = await this.$http.get(
        `categories/${this.getCateId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品属性失败')
      }
      // 将数据里面的attr_vals变成数组格式的数据
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 每一项的表单值显示隐藏单独区分
        item.inputVisible = false
        // 每一项表单值的值单独处理
        item.inputValue = ''
      })
      console.log(res.data)
      // 判断当前单击的是哪个属性
      if (this.activeName === 'many') {
        this.manyAttrList = res.data
      } else {
        this.onlyAttrList = res.data
      }
    },
    // 级联框的值发生改变时数据处理
    handleChange() {
      // 获取对应的商品分类属性
      this.getAttrList()
    },
    // tab栏切换的事件处理
    handleClick() {
      // 获取对应的商品分类属性
      this.getAttrList()
      // 顺便把选择的参数赋给表单的值
      this.addParamsForm.attr_sel = this.activeName
    },
    // 显示出添加参数的弹出框
    showAddParamsDialog() {
      this.addParamsDialog = true
    },
    // 重置添加属性表单
    resetAddParams() {
      this.addParamsDialog = false
      // 然后重置表单和验证规则
      this.$refs.addParamsForm.resetFields()
    },
    // 添加动态属性或者静态参数
    addParams() {
      // 先对表单进行验证
      this.$refs.addParamsForm.validate(async (vaild) => {
        //   验证失败
        if (!vaild) return null
        // 添加属性
        const { data: res } = await this.$http.post(
          `categories/${this.getCateId}/attributes`,
          this.addParamsForm
        )
        // 添加属性失败
        if (res.meta.status !== 201) { return this.$message.error('添加' + this.getActiveTabName + '失败') }
        // 关闭弹出框
        this.addParamsDialog = false
        this.$message.success('添加' + this.getActiveTabName + '成功')
        // 刷新数据
        // 获取对应的商品分类属性
        this.getAttrList()
      })
    },
    // 展示出编辑弹出框
    async showEditParamsDialog(row) {
      // 先进行查询，然后在弹出
      // 获取本id数据的信息
      const { data: res } = await this.$http.get(
        `categories/${this.getCateId}/attributes/${row.attr_id}`,
        {
          params: {
            attr_sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) { return this.$message.error('根据id查询数据失败') }
      this.editParamsForm = res.data
      // 显示弹出框
      this.editParamsDialog = true
    },
    // 重置编辑属性表单
    resetEditParams() {
      this.editParamsDialog = false
      // 然后重置表单和验证规则
      this.$refs.editParamsForm.resetFields()
    },
    // 编辑动态属性或者静态参数
    editParams() {
      // 先对表单进行验证
      this.$refs.editParamsForm.validate(async (vaild) => {
        //   验证失败
        if (!vaild) return null
        // 修改属性
        const { data: res } = await this.$http.put(
          `categories/${this.getCateId}/attributes/${this.editParamsForm.attr_id}`,
          {
            attr_name: this.editParamsForm.attr_name,
            attr_sel: this.editParamsForm.attr_sel
          }
        )
        // 编辑属性失败
        if (res.meta.status !== 200) { return this.$message.error('修改' + this.getActiveTabName + '失败') }
        // 关闭弹出框
        this.editParamsDialog = false
        this.$message.success('修改' + this.getActiveTabName + '成功')
        // 刷新数据
        // 获取对应的商品分类属性
        this.getAttrList()
      })
    },
    // 删除静态参数或者动态属性
    async deleteParams(row) {
      // 弹窗多次确认
      const result = await this.$confirm(
        '此操作将永久删除该属性, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      if (result === 'cancel') {
        return null
      }
      const { data: res } = await this.$http.delete(
        `categories/${this.getCateId}/attributes/${row.attr_id}`
      )
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success('删除属性成功')
      // 刷新数据
      this.getAttrList()
    },
    // 保存数据库操作
    async saveToData(row) {
      // 然后将数据提交到后台实现数据的保持性
      const { data: res } = await this.$http.put(`categories/${this.getCateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) { return this.$message.error('修改参数项失败') }
      this.$message.success('修改参数项成功')
    },
    // 回车或者失去焦点时的数据处理
    handleInputConfirm (row) {
      // 先取反值 隐藏表单
      row.inputVisible = false
      // 然后将最新的添加值存到数组里面实现动态渲染里面
      // 要先判断输入的是否为有效值
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        return null
      }
      row.attr_vals.push(row.inputValue)
      // 然后清空表单值
      row.inputValue = ''
      // 然后继续调用保存到数据库的操作
      this.saveToData(row)
    },
    // 删除参数项
    handleClose(i, row) {
      // 删除当前位置的参数项
      row.attr_vals.splice(i, 1)
      // 然后继续调用保存到数据库的操作
      this.saveToData(row)
    },
    // 点击按钮显示表单
    showInput(row) {
      // 点击添加按钮显示表单
      row.inputVisible = true
      // $this.$nextTick是页面元素重新渲染好之后触发函数
      this.$nextTick(_ => {
        // 表单自动聚焦
        this.$refs.saveTagInput.$refs.input.focus()
      })
    }
  },
  computed: {
    // 添加计算属性来查看按钮是否禁用
    isBtnDisabled() {
      // 判断上面的级联选择框是否选中第三级商品分类
      return this.selectedCate.length !== 3
    },
    // 获取当前选中的商品分类的id
    getCateId() {
      // 如果没有选择第三级商品分类
      // 返回空
      if (this.selectedCate.length !== 3) return null
      // 选择了就会返回绑定数组中的最后一项
      //   实现第三级商品分类的id返回
      return this.selectedCate[this.selectedCate.length - 1]
    },
    // 返回动态参数和静态属性
    getActiveTabName() {
      if (this.activeName === 'only') return '静态属性'
      else return '动态参数'
    }
  }
}
</script>

<style lang="less" scoped>
// 选择商品分类的上下间距
.select-goods {
  margin: 15px 0;
}
// 标签之间应有较大的距离
.el-tag {
  margin: 10px;
}
// 设置显示的表单的长度值
.input-new-tag {
  width: 140px;
}
</style>
